<template>
  <el-table
    :id="refName"
    :ref="refName"
    :key="refreshKey"
    v-loading="loading"
    :data="tableData"
    border
    stripe
    highlight-current-row
    tooltip-effect="dark"
    style="width: 100%"
    :row-key="rowKey"
    :header-cell-style="{background:'#eef1f6',color:'#606266'}"
    :row-class-name="rowClassName"
    :row-style="rowStyle"
    :cell-style="cellStyle"
    :header-row-style="{height: '36px'}"
    :tree-props="treeProps"
    :expand-row-keys="expandRowKeys"
    :height="tableHeight"
    cell-class-name="table_cell"
    :span-method="rowSpanMethod"
    @select="handleSelect"
    @select-all="handleSelectAll"
    @selection-change="handleSelectionChange"
    @row-click="handleRowClick"
    @sort-change="sortChange"
    @expand-change="expandChange"
  >

    <!--  expand expandList -->
    <el-table-column v-if="expandList.length" type="expand">
      <template slot-scope="props">
        <el-form v-if="expandType === 'form'" label-position="left" class="table-expand">
          <el-form-item
            v-for="(expandItem) in expandList"
            :key="expandItem.prop"
            :label="`${expandItem.label}`"
          >
            <!--  props.row[expandItem.prop] -->
            <component :is="getComponentName(expandItem)" :column="expandItem" :scope="props.row" />
          </el-form-item>
        </el-form>
        <div v-if="expandType === 'table'">
          <slot name="expand-table" :scope="props" />
        </div>
      </template>
    </el-table-column>

    <!--  selection reserve-selection  -->
    <el-table-column
      v-if="selection"
      type="selection"
      :reserve-selection="true"
      align="center"
      width="55"
      :fixed="selectionFixed"
      :selectable="selectable ? selectable : null"
    />

    <!--  index -->
    <el-table-column
      v-if="index"
      type="index"
      min-width="60"
      align="center"
      label=""
      :fixed="indexFixed"
    />

    <!-- :  -->
    <!--  -->
    <template v-for="column in columnList">
      <!--  -->
      <table-column
        :key="column.prop"
        :column="column"
      />
    </template>

    <!--  -->
    <el-table-column v-if="showOperate" :fixed="operateFixed" label="" :width="operationWidth" :align="operateColumnAlign">
      <template slot="header">
        <div class="table-operation-header">
          <div>{{ operateColumnName }}</div>
          <!--  -->
          <div class="table-operation-icon-container">
            <!--  -->
            <refresh-icon @click.native="handleRefresh" />
            <!--  -->
            <system-icon v-show="tableColumnSort" @click.native="sortDialogVisible = true" />
          </div>
        </div>
      </template>
      <!--  -->
      <template slot-scope="scope">
        <div style="width: 100%;'text-align': center;">
          <slot :row="scope.row" :index="scope.$index" />
        </div>
      </template>
    </el-table-column>

    <!--  -->
    <template v-if="showEmptyTextSlot" #empty>
      <slot name="empty-slot"></slot>
    </template>

    <!--  -->
    <sort-dialog
      v-model="sortDialogVisible"
      :sort-data="tableColumnSortData"
      v-bind="$attrs"
      v-on="$listeners"
      @submit="sortDialogSubmit"
    />

  </el-table>
</template>

<script>
import TableColumnComponents from './components' // Table
import TableColumn from './TableColumn.vue'
import RefreshIcon from './RefreshIcon.vue'
import SystemIcon from './SystemIcon'
import SortDialog from './SortDialog.vue'

export default {
  name: 'CustomTableColumn',
  components: {
    TableColumn,
    RefreshIcon,
    SystemIcon,
    SortDialog,
    ...TableColumnComponents
  },
  props: {
    refreshKey: {
      type: Number,
      default: 1
    },
    refName: { // ref  id
      type: String,
      default: 'multipleTable'
    },
    /*
    *   tablenumber String
    *   numbertable
    *    'fullscreen'   'half'   'fullscreenTab' +tab  'halfTab' +tab
    *    'extraFullMinus'     'extraHalfMinus'    @props tableHeightMinus table
    */
    tableHeightType: {
      type: [Number, String],
      default: 'fullscreen'
    },
    tableHeightMinus: {
      type: Number,
      default: 0
    },
    hasPagination: { //  table 
      type: Boolean,
      default: true
    },
    hasTopQuery: { //  table 
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: String,
      default: '100%'
    },
    index: {
      type: Boolean,
      default: true
    },
    indexFixed: {
      type: [Boolean, String],
      default: false
    },
    rowKey: {
      type: String,
      default: 'id'
    },
    rowClassName: {
      type: [Function, String],
      default: () => ''
    },
    rowStyle: {
      type: [Function, String],
      default: () => ''
    },
    cellStyle: {
      type: [Function, String],
      default: () => ''
    },
    selection: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: true
    },
    selectionFixed: {
      type: Boolean,
      default: true
    },
    tableSelection: {
      type: Array,
      default: () => []
    },
    expand: {
      type: Boolean,
      default: false
    },
    expandList: {
      type: Array,
      default: () => {
        return []
      }
    },
    expandType: {
      type: String,
      default: 'form'
    },
    treeProps: {
      type: Object,
      default: () => {
        return { children: 'children', hasChildren: 'hasChildren' }
      }
    },
    expandRowKeys: {
      type: Array,
      default: () => []
    },
    tableData: {
      type: Array,
      default: () => {
        return []
      }
    },
    columnList: {
      type: Array,
      default: () => {
        return []
      }
    },
    tableColumnSort: {
      type: Boolean,
      default: true
    },
    tableColumnSortData: {
      type: Array,
      default: () => {
        return []
      }
    },
    operateFixed: {
      type: [Boolean, String],
      default: 'right'
    },
    showOperate: {
      type: Boolean,
      default: true
    },
    operationWidth: {
      type: Number,
      default: 270
    },
    operateColumnName: {
      type: String,
      default: ''
    },
    operateColumnAlign: {
      type: String,
      default: 'center'
    },
    rowMergeArr: {
      type: Array,
      default: () => []
    },
    //  type=selection  FunctionFunction  CheckBox 
    selectable: {
      type: Function,
      default: () => true
    },
    // 
    selectedInit: {
      type: Boolean,
      default: true
    },
    showEmptyTextSlot: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      tableHeight: '400',
      renderKey: 1,
      sortable: null,
      sortDialogVisible: false,
      tableSelectionList: [],
      mergeObj: {} // 
    }
  },
  watch: {
    $route(route) {
      // if you go to the redirect page, do not update table layout
      this.doLayout()
    },
    tableSelection: {
      handler: function(newVal) {
        this.tableSelectionList = [...newVal]
      },
      immediate: true,
      deep: true
    },
    tableData: {
      handler: function(newVal) {
        this.renderKey++
        this.rowMergeArr.length ? this.rowspan() : null
        if (this.selectedInit) {
          this.$nextTick(() => {
            this.initSelection()
            this.doLayout()
          })
        }
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    this.handleInitTableHeight()
  },
  updated() {
    console.log('handle table component updated')
    this.doLayout()
  },
  methods: {
    /*
    *  
    */
    handleInitTableHeight() {
      let height = 0
      if (typeof this.tableHeightType === 'number') { // 
        height = this.tableHeightType
      } else {
        const innerHeight = window.innerHeight //  
        const table = document.getElementById(this.refName)
        const offsetTop = table.getBoundingClientRect().top // table
        const tableHeaderHeight = table.querySelector('.el-table__header-wrapper').offsetHeight // 
        if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'half') { // 
          const h = (innerHeight - offsetTop) / 2 - tableHeaderHeight
          height = this.hasPagination ? Math.floor(h - 74) : Math.floor(h - 35)
        } else if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'halfTab') { // +tap
          const h = (innerHeight - offsetTop) / 2 - tableHeaderHeight - 40
          height = this.hasPagination ? Math.floor(h - 74) : Math.floor(h - 35)
        } else if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'fullscreenTab') { // +tap
          const h = innerHeight - offsetTop - tableHeaderHeight - 40
          height = this.hasPagination ? Math.floor(h - 56) : Math.floor(h - 10)
          if (this.hasTopQuery) { // 
            height = height - 40
          }
        } else if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'fullscreen') { // 
          const h = innerHeight - offsetTop - tableHeaderHeight
          height = this.hasPagination ? Math.floor(h - 56) : Math.floor(h - 10)
          if (this.hasTopQuery) { // 
            height = height - 40
          }
        } else if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'extraHalfMinus') { // -
          const h = (innerHeight - offsetTop) / 2 - tableHeaderHeight - this.tableHeightMinus
          height = this.hasPagination ? Math.floor(h - 74) : Math.floor(h - 35)
        } else { //  - 
          const h = innerHeight - offsetTop - tableHeaderHeight - this.tableHeightMinus
          height = this.hasPagination ? Math.floor(h - 56) : Math.floor(h - 10)
          if (this.hasTopQuery) { // 
            height = height - 40
          }
        }
      }
      this.$nextTick(() => {
        // this.tableHeight = window.innerHeight - 240
        this.tableHeight = height
      })
    },
    sortDialogSubmit() {
      console.log('')
    },
    // 
    handleRefresh() {
      this.$emit('refresh')
    },
    // 
    handleRowClick(row, column, cell, event) {
      this.$emit('rowClick', { row, column, cell, event })
    },
    sortChange({ column, prop, order }) {
      this.$emit('sortChange', { column, prop, order })
    },
    expandChange(row) {
      this.$emit('expandChange', row)
    },
    // 
    getComponentName(column) {
      const is = column.module
      return `${is.charAt(0).toUpperCase()}${is.slice(1)}Com`
    },
    // 
    initSelection() {
      const ids = this.tableSelection.map(i => i[this.rowKey])
      const rowKey = this.rowKey
      this.tableData.forEach(row => {
        function check(row) {
          const id = row[rowKey]
          return ids.some(i => i === id)
        }
        if (check(row)) {
          this.$nextTick(() => {
            this.$refs[this.refName].toggleRowSelection(row, true)
          })
        } else {
          this.$nextTick(() => {
            this.$refs[this.refName].toggleRowSelection(row, false)
          })
        }
      })
    },
    // 
    clearSelection() {
      this.$refs[this.refName].clearSelection()
    },
    // 
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs[this.refName].toggleRowSelection(row)
        })
      } else {
        this.$refs[this.refName].clearSelection()
      }
    },
    /*
    *  
    *  selection 
    * */
    handleSelectAll(selection) {
      // 
      if (!this.multiple) {
        this.$refs[this.refName].clearSelection()
        this.tableSelectionList = new Array(0)
      }
    },
    /*
    *  
    *  tableSelection 
    * */
    handleSelectionChange(val) {
      if (this.multiple) {
        this.tableSelectionList = val
        console.log('table', this.tableSelectionList)
        this.$emit('selectionChange', this.tableSelectionList)
      }
    },
    /*
    * 
    * 1. multiplefalse
    */
    handleSelect(selection, row) {
      // ()
      this.$emit('dynamicRow', row)
      if (!this.multiple) {
        console.log('table', [row])
        // 
        this.$refs[this.refName].clearSelection()
        // 
        this.$refs[this.refName].toggleRowSelection(row)
        this.$emit('selectionChange', [row])
      }
    },
    /*
    *  
    * @params row Object 
    * @params type Boolean 
    */
    handleSetCurrentRow(row, type) {
      this.$refs[this.refName].setCurrentRow(row, type)
    },
    /*
    * props rowClassName rowClassNameStringFunction
    */
    handleRowClassName({ row, rowIndex }) {
      return typeof this.rowClassName === 'string' ? this.rowClassName : this.rowClassName({ row, rowIndex })
    },
    /*
    *  Tip
    */
    rowspan() {
      for (const key of this.rowMergeArr) {
        let count = 0
        this.mergeObj[key] = []
        this.tableData.forEach((item, index) => {
          if (index === 0) {
            this.mergeObj[key].push(1)
          } else {
            if (item[key] === this.tableData[index - 1][key]) {
              this.mergeObj[key][count] += 1
              this.mergeObj[key].push(0)
            } else if (item[this.rowKey] === this.tableData[index - 1][this.rowKey]) {
              this.mergeObj[key][count] += 1
              this.mergeObj[key].push(0)
            } else {
              count = index
              this.mergeObj[key].push(1)
            }
          }
        })
      }
    },
    // Tip 
    rowSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (this.rowMergeArr.indexOf(column.property) !== -1) {
        if (this.mergeObj[column.property][rowIndex]) {
          return {
            rowspan: this.mergeObj[column.property][rowIndex],
            colspan: 1
          }
        } else {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      } else {
        return {
          rowspan: 1,
          colspan: 1
        }
      }
    },
    /*
    *   Table  Table  fixed
    */
    doLayout() {
      console.log('table ')
      this.$nextTick(() => {
        if (this.$refs[this.refName]) {
          this.$refs[this.refName].doLayout()
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.table-expand {
    font-size: 0;
    margin-left: 63px;
}
.table-expand label {
    width: 190px;
    color: #99a9bf;
}
.table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
}
::v-deep .el-table::before {
  z-index: inherit; //  z-index 
}
</style>
