/*
*		:
*   
*   import paginationPage from '@/mixins/paginationPage'
*   :
*   mixins: [paginationPage]
*/

import TableCom from '@/components/Table'
import Pagination from '@/components/Pagination'

export default {
  components: {
    TableCom,
    Pagination
  },
  data() {
    return {
      // 
      pageInfo: {
        current: 1, // 
        size: 50 // 
      },
      pages: 1, // 
      total: 0// 
    }
  },
  methods: {
    // 
    searchInit() {
      this.pageInfo = {
        current: 1, // 
        size: 50 // 
      }
      this.init()
    },
    initPage(pageInfo) {
      const { total, pageNum, pageSize } = pageInfo
      this.pageInfo.current = pageNum
      this.pageInfo.size = pageSize
      this.total = total
    },
    indexMethod(index) {
      const { current, size } = this.pageInfo
      return (index + 1) + (current - 1) * size
    },
    resetPage() {
      this.pageInfo = {
        current: 1, // 
        size: 50 // 
      }
      this.total = 0
    }
  }
}
